.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  visibility: visible;
  transition: opacity var(--time) var(--timing-function),
    visibility var(--time) var(--timing-function);
    z-index: 999;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal {
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 88%;
  padding: 28px 20px;
  background-color: var(--color-primary);
  border: 2px solid #F8D24E;
  border-radius: 10px;
  @media screen and (min-width:648px) {
    width: 568px;
    padding: 32px 40px;
  }
}
.modal__icon {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.modal__title {
  font-family: var(--secondary-font);
  font-weight: 400;
  font-size: 20px;
  line-height: 1.4;
  color: var(--color-secondary);
  margin-bottom: 20px;
}

.modal__img--wrap {
  position: relative;
}

.modal__img {
  display: inline-block;
  margin-bottom: 29px;
  @media screen and (min-width:648px) {
    margin-bottom: 31px;
  }
}
.modal__img--wrap::after {
    content: "";
    display: block;
    position: absolute;
    width: 132px;
    height: 132px;
    border-radius: 50%;
    top: 7px;
    left: 50%;
    transform: translate(-50%);
    background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #D9D9D9;
      box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
      z-index: -99;
}

.composition {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 9px;
  margin-bottom: 40px;
  @media screen and (min-width:648px) {
    gap: 20px;
    margin-bottom: 48px;
  }
}

.composition__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 53px;
  border: 2px solid #9CBE8F;
  border-radius: 40px;
  padding-bottom: 8px;
  @media screen and (min-width:648px) {
    width: 65px;
    padding-bottom: 15px;
  }
}

.composition__weight {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 43px;
  height: 43px;
  background: #9CBE8F;
  border: 2px solid #9CBE8F;
  border-radius: 50%;
  padding-top: 8px;
  padding-bottom: 8px;
  margin-bottom: 8px;
  box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
  @media screen and (min-width:648px) {
    width: 53px;
    height: 53px;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-bottom: 7px;
  }
}

.composition__namber {
  font-weight: 500;
  font-size: 10px;
  line-height: 2.8;
  @media screen and (min-width:648px) {
    font-size: 12px;
    line-height: 2.33;
  }
}

.composition__unit-measurement {
  font-size: 10px;
  line-height: 1.2;
  color: #131313;
  @media screen and (min-width:648px) {
    font-size: 12px;
    line-height: 1.25;
  }
}

.modal__subtitle {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  margin-bottom: 24px;
}

.ingredients {
  margin-bottom: 40px;
}

.ingredients__item {
  display: flex;
  justify-content: space-between;
  line-height: 1.71;
}

.form__input {
  width: 100%;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 10px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid var(--color-bg);
  border-radius: 10px;
  margin-bottom: 16px;
  @media screen and (min-width:648px) {
    width: 358px;
    padding-left: 16px;
  }
}

.form__textarea {
  width: 100%;
  height: 84px;
  padding-top: 11px;
  padding-left: 11px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid var(--color-bg);
  border-radius: 10px;
  margin-bottom: 24px;
  resize: none;
  @media screen and (min-width:648px) {
    width: 358px;
    padding-left: 16px;
  }
}

.form__input::placeholder,
.form__textarea::placeholder {
  line-height: 1.4;
  color: var(--color-secondary);
}

.modal__button {
  width: 132px;
  padding: 12px 34px;
  background: var(--color-accent);
  border-radius: 10px;
  border: none;
  font-weight: 600;
  line-height: 1.21;
  color: var(--color-secondary);
}